import React, { useState } from 'react';
import { MapContainer, TileLayer, CircleMarker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import waterSystemsData from '../data/waterSystemsData';
import './LeadLineMap.css';

function LeadLineMap() {
  const [showNoInventory, setShowNoInventory] = useState(true);
  const [showNoLead, setShowNoLead] = useState(true);
  const [showCompliant, setShowCompliant] = useState(true);
  const [showNonCompliant, setShowNonCompliant] = useState(true);

  // Filter systems with coordinates
  const systemsWithCoords = waterSystemsData.filter(
    system => system.latitude && system.longitude
  );

  // Get compliance category for a system
  const getComplianceCategory = (system) => {
    // 1. No inventory filed
    if (!system.inventoryFiled) return 'no-inventory';
    // 2. No lead lines identified
    if (system.totalToReplace === 0) return 'no-lead';
    // 3. Compliant (20% progress)
    if (system.percentReplaced >= 20) return 'compliant';
    // 4. Not in Compliance (<20% progress)
    return 'not-compliant';
  };

  // Filter by compliance status
  const filteredSystems = systemsWithCoords.filter(system => {
    const category = getComplianceCategory(system);
    switch (category) {
      case 'no-inventory': return showNoInventory;
      case 'no-lead': return showNoLead;
      case 'compliant': return showCompliant;
      case 'not-compliant': return showNonCompliant;
      default: return true;
    }
  });

  // Determine marker color based on compliance
  const getMarkerColor = (system) => {
    const category = getComplianceCategory(system);
    switch (category) {
      case 'no-inventory': return '#9333ea'; // Purple for no inventory filed
      case 'no-lead': return '#3b82f6';      // Blue for no lead lines identified
      case 'compliant': return '#16a34a';    // Green for compliant
      case 'not-compliant': return '#dc2626'; // Red for not compliant
      default: return '#6b7280';
    }
  };

  // Calculate marker radius based on total lines to replace
  const getMarkerRadius = (system) => {
    // For non-filers, use a fixed size since we don't know their data
    if (!system.inventoryFiled) return 6;
    // Scale radius: sqrt for better visual proportion
    const baseRadius = Math.sqrt(system.totalToReplace) / 5;
    return Math.max(baseRadius, 4); // Minimum 4px radius
  };

  // Get compliance status text
  const getComplianceStatus = (system) => {
    const category = getComplianceCategory(system);
    switch (category) {
      case 'no-inventory': return 'No complete inventory filed';
      case 'no-lead': return 'No lead lines identified';
      case 'compliant': return 'Compliant';
      case 'not-compliant': return 'Not in Compliance';
      default: return 'Unknown';
    }
  };

  // Count systems by category
  const counts = {
    noInventory: systemsWithCoords.filter(s => getComplianceCategory(s) === 'no-inventory').length,
    noLead: systemsWithCoords.filter(s => getComplianceCategory(s) === 'no-lead').length,
    compliant: systemsWithCoords.filter(s => getComplianceCategory(s) === 'compliant').length,
    notCompliant: systemsWithCoords.filter(s => getComplianceCategory(s) === 'not-compliant').length
  };

  return (
    <div className="map-container">
      <h2>Geographic Distribution of Lead Service Line Replacement Compliance in Michigan</h2>
      <p className="map-subtitle">
        Showing {filteredSystems.length} of {systemsWithCoords.length} water systems with location data
      </p>

      {/* Filter Controls */}
      <div className="map-controls">
        <label>
          <input
            type="checkbox"
            checked={showNoInventory}
            onChange={(e) => setShowNoInventory(e.target.checked)}
          />
          <span className="no-inventory-label"> No complete inventory filed ({counts.noInventory})</span>
        </label>
        <label>
          <input
            type="checkbox"
            checked={showNoLead}
            onChange={(e) => setShowNoLead(e.target.checked)}
          />
          <span className="no-lead-label"> No lead lines identified ({counts.noLead})</span>
        </label>
        <label>
          <input
            type="checkbox"
            checked={showCompliant}
            onChange={(e) => setShowCompliant(e.target.checked)}
          />
          <span className="compliant-label"> Compliant ({counts.compliant})</span>
        </label>
        <label>
          <input
            type="checkbox"
            checked={showNonCompliant}
            onChange={(e) => setShowNonCompliant(e.target.checked)}
          />
          <span className="noncompliant-label"> Not in Compliance ({counts.notCompliant})</span>
        </label>
      </div>

      {/* Map */}
      <MapContainer
        center={[44.3148, -85.6024]} // Center of Michigan
        zoom={7}
        style={{ height: '600px', width: '100%' }}
        scrollWheelZoom={true}
      >
        {/* Base map tiles from OpenStreetMap */}
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />

        {/* Water system markers */}
        {filteredSystems.map((system) => (
          <CircleMarker
            key={system.pwsid}
            center={[system.latitude, system.longitude]}
            radius={getMarkerRadius(system)}
            fillColor={getMarkerColor(system)}
            fillOpacity={0.6}
            color="#ffffff"
            weight={2}
          >
            <Popup>
              <div className="map-popup">
                <h3>{system.name}</h3>
                <div className="popup-stats">
                  <p><strong>PWSID:</strong> {system.pwsid}</p>
                  <p><strong>Population:</strong> {system.population.toLocaleString()}</p>
                  {system.inventoryFiled ? (
                    <>
                      <p><strong>Lead Lines:</strong> {system.leadLines.toLocaleString()}</p>
                      <p><strong>Total to Replace:</strong> {system.totalToReplace.toLocaleString()}</p>
                      <p><strong>Replaced:</strong> {system.totalReplaced.toLocaleString()}</p>
                      <p><strong>Progress:</strong> {system.percentReplaced.toFixed(1)}%</p>
                    </>
                  ) : (
                    <p className="no-inventory-warning">
                      <strong> No CDSMI inventory filed</strong>
                    </p>
                  )}
                  <p>
                    <strong>Status:</strong>{' '}
                    <span style={{ 
                      color: getMarkerColor(system),
                      fontWeight: 'bold'
                    }}>
                      {getComplianceStatus(system)}
                    </span>
                  </p>
                  {system.exceedance && system.exceedance !== '-' && (
                    <p><strong>LCR Exceedance:</strong> {system.exceedance}</p>
                  )}
                  {system.epaLink && (
                    <p className="epa-link">
                      <a 
                        href={system.epaLink} 
                        target="_blank" 
                        rel="noopener noreferrer"
                        className="epa-link-button"
                      >
                        View EPA Facility Report 
                      </a>
                    </p>
                  )}
                </div>
              </div>
            </Popup>
          </CircleMarker>
        ))}
      </MapContainer>

      {/* Legend */}
      <div className="map-legend">
        <h4>Legend</h4>
        <div className="legend-items">
          <div className="legend-item">
            <span className="legend-circle no-inventory"></span>
            <span>No complete inventory filed</span>
          </div>
          <div className="legend-item">
            <span className="legend-circle no-lead"></span>
            <span>No lead lines identified</span>
          </div>
          <div className="legend-item">
            <span className="legend-circle compliant"></span>
            <span>Compliant (20% replaced)</span>
          </div>
          <div className="legend-item">
            <span className="legend-circle noncompliant"></span>
            <span>Not in Compliance (&lt;20% replaced)</span>
          </div>
          <div className="legend-item">
            <span className="legend-text">Circle size = Total lead lines to be identified or replaced</span>
          </div>
        </div>
      </div>

      {/* Info Box */}
      <div className="map-info">
        <p>
          <strong>Note:</strong> This map shows {systemsWithCoords.length} water systems 
          ({(systemsWithCoords.length / waterSystemsData.length * 100).toFixed(1)}% of all Michigan systems) 
          with verified location data from EPA community water system boundaries. Click on any circle to see detailed information and access the EPA facility report.
        </p>
      </div>
    </div>
  );
}

export default LeadLineMap;
